import { IRequestState } from "../types/request";

const createlUrlFetchAll = (request: IRequestState): string => {
  const base = "https://the-one-api.dev/v2/";

  const category = "character";

  const search = `?name=/${request.content}/i`;

  const page = `&page=${request.page}`;

  const limit = `&limit=${request.limit}`;

  const sort = `&sort=${request.sortBy}:${request.order}`;

  const url = base + category + search + page + limit + sort;

  return url;
};

export default createlUrlFetchAll;
