import React from "react";

import useActions from "../../../hooks/useActions";
import useTypedSelector from "../../../hooks/useTypedSelector";

const InputSearch: React.FC = () => {
  const { setRequest } = useActions();

  const request = useTypedSelector(state => state.request);

  const name = "content";

  const setInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    request[name] = event.target.value;

    setRequest(request);
  };

  const checkKey = (event: React.KeyboardEvent) => {
    if (event.key === "Enter") {
      request.page = 1;
      request.isSubmited = true;

      setRequest(request);
    }
  };

  return (
    <div className="form__input-wrap">
      <input
        onChange={setInput}
        onKeyPress={checkKey}
        value={request.content}
        name={name}
        className="form__input"
        type="search"
        id="input-text"
      />
    </div>
  );
};

export default InputSearch;
