import React from "react";
import { v4 as uuidv4 } from "uuid";

import { IApiCharacters } from "../../types/intefaces";

import Card from "./Card";

const CardsContainer: React.FC<{ results: IApiCharacters[] }> = props => {
  const { results } = props;
  return (
    <div className="cards">
      {results.map(result => {
        return <Card data={result} key={uuidv4()} />;
      })}
    </div>
  );
};

export default CardsContainer;
