export enum RequestActionTypes {
  SET_REQUEST = "SET_REQUEST",
  GET_REQUEST = "GET_REQUEST",
}

export interface IRequestState {
  [key: string]: string | number | boolean;
  content: string;
  sortBy: string;
  limit: string;
  order: string;
  page: number;
  isSubmited: boolean;
}

interface ISetRequest {
  type: RequestActionTypes.SET_REQUEST;
  payload: IRequestState;
}

export type RequestAction = ISetRequest;
