import React from "react";
import { BrowserRouter } from "react-router-dom";

import Header from "./components/Header/Header";
import Routes from "./components/Routes";

const App: React.FC = () => {
  return (
    <BrowserRouter>
      <Header />
      <div className="working-zone">
        <Routes />
      </div>
    </BrowserRouter>
  );
};

export default App;
