import React, { useRef } from "react";
import { useParams } from "react-router-dom";

import createlUrlFetch from "../../misc/createUrlFetch";

import Card from "../Cards/Card";
import useFetch from "../../hooks/useFetch";
import useTypedSelector from "../../hooks/useTypedSelector";

const Details: React.FC = () => {
  const { id } = useParams<{ id: string }>();

  const url = createlUrlFetch(id);

  const isSingleRequest = useRef(true);

  const { response, error, isLoading, isResponded } = useTypedSelector(state => state.fetch);

  const request = useTypedSelector(state => state.request);

  request.isSubmited = true;

  useFetch({ url, isSingleRequest });

  return (
    <section className="details no-touch">
      {isLoading && <div className="hourglass" />}
      {error && <p className="error-msg">Sorry, server hasn&apos;t answered. Try again later. </p>}
      {isResponded && response.docs.length && <Card data={response.docs[0]} />}
    </section>
  );
};

export default Details;
