import React from "react";

import useTypedSelector from "../../hooks/useTypedSelector";
import useFetch from "../../hooks/useFetch";

import { sortBy, order, select } from "../../store/vars";

import createlUrlFetchAll from "../../misc/createUrlFetchAll";

import CardsContainer from "../Cards/CardsContatiner";
import InputRadioWrap from "./components/Radio/InputRadioWrap";
import InputSearch from "./components/InputSearch";
import Select from "./components/Select/Select";
import NavButtons from "./components/NavButtons/NavButtons";

const SearchBar: React.FC = () => {
  const { response, error, isLoading, isResponded } = useTypedSelector(state => state.fetch);

  const request = useTypedSelector(state => state.request);

  const url = createlUrlFetchAll(request);

  useFetch({ url });

  const wrapClasses = isLoading ? "search inactive" : "search";

  return (
    <section className={wrapClasses}>
      <h2 className="headline search__headline">Find any LOTR character</h2>
      <InputSearch />
      <InputRadioWrap innerText={sortBy} />
      <InputRadioWrap innerText={order} />
      <Select innerText={select} />
      {!isLoading && !error && <CardsContainer results={response.docs} />}
      {isResponded && !response.docs.length && <p>Sorry, no results for your query</p>}
      {isLoading && <div className="hourglass" />}
      {error && <p className="error-msg">Sorry, server hasn&apos;t answered. Try again later. </p>}
      <NavButtons />
    </section>
  );
};

export default SearchBar;
