import React from "react";

import { IInputProps } from "../../../../types/intefaces";
import { nextButton, prevButton } from "../../../../store/vars";

import useTypedSelector from "../../../../hooks/useTypedSelector";

import Button from "./Button";

const NavButtons: React.FC<IInputProps> = () => {
  const request = useTypedSelector(state => state.request);

  const { pages } = useTypedSelector(state => state.fetch);

  const pagesNum = pages as number;

  return (
    <div className="nav-buttons">
      <h2>
        {request.page}/{pages}
      </h2>
      <Button buttonDataBundle={prevButton} isDisabled={request.page === 1 || pagesNum === 1} />
      <Button buttonDataBundle={nextButton} isDisabled={request.page >= pagesNum} />
    </div>
  );
};

export default NavButtons;
