import { Dispatch } from "react";
import axios from "axios";

import { FetchAction, FetchActionTypes } from "../../types/fetchData";

import { FetchHeader } from "../vars";

export const fetchData = (url: string): ((dispatch: Dispatch<FetchAction>) => Promise<void>) => {
  return async (dispatch: Dispatch<FetchAction>) => {
    try {
      dispatch({ type: FetchActionTypes.FETCH_DATA });

      const response = await axios.get(url, {
        headers: FetchHeader,
      });

      dispatch({ type: FetchActionTypes.FETCH_DATA_SUCCESS, payload: response.data });
    } catch (error) {
      dispatch({ type: FetchActionTypes.FETCH_DATA_ERROR, payload: "error" });
    }
  };
};

export default fetchData;
