import { Dispatch } from "react";

import { IRequestState, RequestAction, RequestActionTypes } from "../../types/request";

export const setRequest = (newRequest: IRequestState): ((dispatch: Dispatch<RequestAction>) => void) => {
  return (dispatch: Dispatch<RequestAction>) => {
    dispatch({ type: RequestActionTypes.SET_REQUEST, payload: newRequest });
  };
};

export default setRequest;
