import { IRequestState, RequestAction, RequestActionTypes } from "../../types/request";

const defaultRequest: IRequestState = {
  content: "",
  sortBy: "name",
  limit: "10",
  order: "desc",
  page: 1,
  pages: 1,
  isSubmited: false,
};

const requestReducer = (state = defaultRequest, action: RequestAction): IRequestState => {
  switch (action.type) {
    case RequestActionTypes.SET_REQUEST:
      return {
        ...action.payload,
      };

    default:
      return state;
  }
};

export default requestReducer;
