import React from "react";
import useActions from "../../../../hooks/useActions";
import useTypedSelector from "../../../../hooks/useTypedSelector";

import { IRadio } from "../../../../types/intefaces";

const InputRadio: React.FC<IRadio> = props => {
  const { setRequest } = useActions();

  const request = useTypedSelector(state => state.request);

  const { name, labelText } = props;

  const handleClick = (e: React.ChangeEvent<HTMLInputElement>) => {
    request[name] = e.target.value;

    request.isSubmited = true;

    setRequest(request);
  };

  return (
    <>
      <label className="form__input-label" htmlFor={name + labelText}>
        {labelText}

        <input
          type="radio"
          className="form__input-radio"
          id={name + labelText}
          checked={labelText === request[name]}
          value={labelText}
          name={name}
          onChange={handleClick}
        />
      </label>
    </>
  );
};

export default InputRadio;
