import { useEffect, useRef } from "react";

import { IFetchParams } from "../types/intefaces";

import useActions from "./useActions";
import useTypedSelector from "./useTypedSelector";

const useFetch = (fetchParams: IFetchParams): void => {
  const { url, isSingleRequest } = fetchParams;

  const isFirstRender = useRef(true);

  const { fetchData } = useActions();

  const { isLoading } = useTypedSelector(state => state.fetch);

  const request = useTypedSelector(state => state.request);

  useEffect(() => {
    if (isFirstRender.current && !isSingleRequest?.current) {
      isFirstRender.current = false;
      return;
    }

    if (!request.isSubmited || isLoading) {
      return;
    }

    request.isSubmited = false;

    fetchData(url);
  }, [request]);
};

export default useFetch;
