import { IFetchState, FetchAction, FetchActionTypes } from "../../types/fetchData";
import { defaultResponse } from "../vars";

const initialState: IFetchState = {
  response: defaultResponse,
  isLoading: false,
  isResponded: false,
  error: null,
  pages: 1,
};

const fetchDataReducer = (state = initialState, action: FetchAction): IFetchState => {
  switch (action.type) {
    case FetchActionTypes.FETCH_DATA:
      return { ...initialState, isLoading: true, pages: state.pages };
    case FetchActionTypes.FETCH_DATA_SUCCESS:
      return {
        ...initialState,
        response: action.payload,
        pages: action.payload.pages,
        isResponded: true,
      };
    case FetchActionTypes.FETCH_DATA_ERROR:
      return { ...initialState, error: action.payload };

    default:
      return state;
  }
};

export default fetchDataReducer;
