import React from "react";
import useActions from "../../../../hooks/useActions";
import useTypedSelector from "../../../../hooks/useTypedSelector";

import { IButtonDataBundle } from "../../../../types/intefaces";

const Button: React.FC<IButtonDataBundle> = props => {
  const request = useTypedSelector(state => state.request);

  const { setRequest } = useActions();

  const { buttonDataBundle, isDisabled } = props;

  const { name, classes, innerText } = buttonDataBundle;

  const wrapClasses = isDisabled ? "form__submit-wrap inactive" : "form__submit-wrap";

  const changePage = () => {
    const newPage = innerText === "Next" ? request.page + 1 : request.page - 1;

    request.page = newPage;

    request.isSubmited = true;

    setRequest(request);
  };
  return (
    <div className={wrapClasses}>
      <button name={name} className={classes} onClick={changePage} type="button">
        {innerText}
      </button>
    </div>
  );
};

export default Button;
