export enum FetchActionTypes {
  FETCH_DATA = "FETCH_DATA",
  FETCH_DATA_SUCCESS = "FETCH_DATA_SUCCESS",
  FETCH_DATA_ERROR = "FETCH_DATA_ERROR",
}

export interface IApiResponse {
  docs: IApiCharacters[];
  page: number;
  pages: number;
}

export interface IApiCharacters {
  _id: string;
  birth: string;
  death: string;
  name: string;
  gender: string;
  race: string;
}

export interface IFetchState {
  response: IApiResponse;
  isLoading: boolean;
  isResponded: boolean;
  error: null | string;
  pages: number;
}

interface IFetchDataAction {
  type: FetchActionTypes.FETCH_DATA;
}

interface IFetchDataActionSuccess {
  type: FetchActionTypes.FETCH_DATA_SUCCESS;
  payload: IApiResponse;
}

interface IFetchDataActionError {
  type: FetchActionTypes.FETCH_DATA_ERROR;
  payload: string | null;
}

export type FetchAction = IFetchDataAction | IFetchDataActionSuccess | IFetchDataActionError;
