import React from "react";
import useActions from "../../../../hooks/useActions";
import useTypedSelector from "../../../../hooks/useTypedSelector";

import { IInnerText, IInputProps, IOption } from "../../../../types/intefaces";

import SelectOption from "./SelectOption";

const Select: React.FC<IInputProps> = props => {
  const { setRequest } = useActions();

  const request = useTypedSelector(state => state.request);

  const { innerText } = props;

  const { name, labels, headline } = innerText as IInnerText;

  const options = labels as IOption[];

  const getSelectState = (e: React.ChangeEvent<HTMLSelectElement>) => {
    request[name] = e.target.value;

    request.page = 1;

    request.isSubmited = true;

    setRequest(request);
  };

  return (
    <div className="form__input-wrap">
      <label className="form__input-label" htmlFor="select">
        {headline}
        <select onChange={getSelectState} className="form__select" name={name} id="select">
          {options.map(option => {
            return <SelectOption option={option} key={option.id} />;
          })}
        </select>
      </label>
    </div>
  );
};

export default Select;
