import React from "react";
import { Link } from "react-router-dom";

import { IApiCharacters } from "../../types/intefaces";

const Card: React.FC<{ data: IApiCharacters }> = props => {
  const { data } = props;

  const { _id: id, birth, death, name, race, gender } = data;

  const classes = "cards__card";

  const checkData = (info: string): string => {
    return info && info !== "NaN" ? info : "No data";
  };

  return (
    <Link to={`details/${id}`}>
      <div className={classes}>
        <h2 className="cards__card__headline">{name}</h2>
        <p className="cards__card__field">
          {checkData(birth)} - {checkData(death)}
        </p>
        <p className="cards__card__field">{checkData(race)}</p>
        <p className="cards__card__field">{checkData(gender)}</p>
      </div>
    </Link>
  );
};

export default Card;
